.smallProfile {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40rem;
    height: 10rem;
    margin-top: 12rem;

    @media only screen and (max-width:$bp-largest) {
        opacity: 0;
    }

    @media only screen and (max-width: $bp-small) {
        width: 100vw;
    }

    &__container {
        display: block;
    }

    &__img {
        width: 10rem;
        height: 10rem;
        padding: 1rem;
        border-radius: 50%;
        object-fit: cover;
    }

    &__data {
        margin: 2rem 1rem;

        &-username {
            color: white;
            font-size: 1.6rem;
        }

        &-name {
            color: $color-grey-light-2;
            font-size: 1.6rem;
        }
    }

    &__button {
        display: flex;
        align-items: center;

        &-btn {
            padding-left: 6rem;
            border: none;
            background-color: $color-primary-1;
            color: $color-btn;
            cursor: pointer;
            font-size: 1.3rem;
        }
    }

}

.profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 5rem 0;
    margin-left: 20rem;

    @media only screen and (max-width: $bp-largest) {
        margin-left: 5rem;
    }

    @media only screen and (max-width: $bp-small) {
        margin-left: 0rem;
        margin-top: 8rem;
    }

    &__name {
        font-size: 5rem;
        margin-bottom: -3rem;
    }

    &__top {

        &-content {
            display: flex;
            width: 100%;
        }

        &-img {
            width: 14rem;
            height: 14rem;
            border-radius: 50%;
            margin-right: 10rem;
            object-fit: cover;


            @media only screen and (max-width: $bp-largest) {
                width: 10rem;
                height: 10rem;
                margin-right: 0;
                margin: 0 3rem;
            }
        }

        &-information {
            width: 100%;
        }

        &-main {
            display: flex;
            align-items: center;
            justify-content: flex-start;
        }

        &-username {
            font-size: 2rem;
            font-weight: lighter;
        }

        &-edit {
            padding: .5rem 1rem;
            background-color: inherit;
            border: 1px solid $color-grey-dark-2;
            border-radius: 3px;
            color: white;
            margin-left: 3rem;
            cursor: pointer;
        }

        &-follow {
            padding: .5rem 1rem;
            background-color: $color-btn;
            border: 1px solid $color-grey-dark-2;
            border-radius: 3px;
            color: white;
            margin-left: 3rem;
            cursor: pointer;
        }

        &-followed {
            padding: .5rem 1rem;
            background-color: white;
            border: 1px solid $color-grey-dark-2;
            border-radius: 3px;
            color: black;
            margin-left: 3rem;
            cursor: pointer;
        }

        &-statistics {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 2rem 0;
            width: 40%;
            font-size: 1.4rem;
        }

        &-count {
            cursor: pointer;
            margin-right: 2rem;
            color: white !important;
        }
    }

    &__biography {
        font-size: 1.4rem;
        width: 60%;
        margin: 1rem 5rem;
        height: auto;
    }

    &__container {
        border-top: 1px solid $color-grey-light-2;
        width: 60%;
        margin-top: 6rem;


        @media only screen and (max-width: $bp-largest) {
            width: 80%;
        }

        &-title {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 1.8rem;
            margin: 2rem 0;
        }

        &-icon {
            transform: scale(1.8);
            margin-right: 0.5rem;
        }

        &-content {
            display: grid;
            grid-template-rows: masonry;
            align-content: stretch;
            grid-template-columns: repeat(3, 1fr);
            grid-gap: 2rem;
        }

        &-image {
            object-fit: cover;
            width: 100%;
            cursor: pointer;
        }
    }
}

.editProfile {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    &__container {
        margin-top: 5rem;
        display: flex;
        background-color: black;
        height: 90vh;
        width: 60vw;
        margin-left: 10rem;

        @media only screen and (max-width:$bp-largest) {
            display: block;
        }

        @media only screen and (max-width: $bp-small) {
            width: 100%;
            height: 100%;
            margin: 0;
            margin-top: 10rem;
        }
    }

    &__sidebar {
        font-size: 1.4rem;
        color: white;
        cursor: pointer;
        flex: 1;
        border-right: 1px solid $color-grey-dark-1;

        &-point {
            padding: 1rem 2rem;

            &:hover {
                background-color: $color-grey-dark-2;
            }
        }
    }

    &__content {
        flex: 3;
        background-color: black;
    }

    color: white;

    &__avatar {
        font-size: 1.6rem;
        margin-left: 5rem;
        padding: 2rem;
        display: flex;
        align-items: center;

        &-picture {
            border-radius: 50%;
            height: 6rem;
            margin-right: 4rem;
            object-fit: cover;
            width: 6rem;
        }

        &-btn {
            color: $color-btn;
            cursor: pointer;
            font-size: 1.4rem;
            font-weight: bold;
        }
    }

    &__body {
        display: flex;
        width: 100%;
        padding: 2rem;
        margin-bottom: 3rem;

        @media only screen and (max-width:$bp-largest) {
            display: block;
            text-align: center;
        }

        &-term {
            font-size: 1.4rem;
            width: 45%;
            font-weight: bold;

            @media only screen and (max-width:$bp-largest) {
                text-align: center;
                width: 100%;
                margin-bottom: 1rem;
            }
        }

        &-comment {
            font-size: 1.4rem;
            width: 40%;
            float: right;
            margin-right: 30%;
            color: $color-grey-light-2;
            margin-top: -3rem;
            margin-bottom: 3rem;

            @media only screen and (max-width:$bp-largest) {
                display: none;
            }
        }

        &-input {
            width: 60%;
            height: 3.5rem;
            margin-right: 30%;
            background-color: inherit;
            color: $color-white;
            font-size: 1.4rem;
            border: 1px solid $color-grey-dark-1;
            padding: 3px;

            @media only screen and (max-width:$bp-largest) {
                margin-right: 0;
            }
        }

        &-input:focus {
            outline: none;
        }

        &-textarea {
            width: 60%;
            margin-right: 30%;
            background-color: inherit;
            color: $color-white;
            font-size: 1.8rem;
            border: 1px solid $color-grey-dark-1;
            padding: 3px;
            resize: none;
            font-size: 1.3rem;

            @media only screen and (max-width:$bp-largest) {
                margin-right: 0;
            }
        }

        &-textarea:focus {
            outline: none;
        }

        &-button {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 5rem;

            @media only screen and (max-width: $bp-largest) {
                margin-right: 0rem;
            }

            @media only screen and (max-width: $bp-small) {
                margin-right: 0rem;
            }
        }
    }
}