// COLOR VARIABLES
$color-primary-1: #121212;
$color-primary-2: #181818;
$color-black: #0f0f0f;
$color-black-2: #1a1a1a;
$color-white: #fff;
$color-grey-light-1: #c4c4c4;
$color-grey-light-2: #666;
$color-grey-dark-1: #292929;
$color-grey-dark-2: #474747;
$color-btn: #267ac4;
$color-btn-hover: #105ea1;
$color-btn-dark: #5f73ad;
$color-btn-light: #0d3a57;
$color-alert-red: #ff0000;
$color-alert-green: #00bf00;
// FONT VARIABLES
$font-primary: 'Nunito',
sans-serif;
$font-display: 'Josefin Sans',
sans-serif;
$font-logo: 'Roboto',
sans-serif;
// RESPONSIVE BREAKPOINTS
$bp-largest: 75em; // 1200px
$bp-large: 62.5em; // 1000px
$bp-medium: 50em; // 800px
$bp-small: 47.5em; // 600px

.alert {
    padding: 0.375rem 0.75rem;
    margin-bottom: 2rem;
    margin: 2rem;
    border-radius: 3px;
    text-align: center;
    text-transform: uppercase;
    font-size: 2rem;
}

.alert-danger {
    color: $color-alert-red;
    border: 1px solid $color-alert-red;
}

.alert-success {
    color: $color-alert-green;
    border: 1px solid $color-alert-green;
}

::-webkit-scrollbar-thumb {
    background: #888;
}

::-webkit-scrollbar {
    width: 10px;
}