.follow {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    &__container {
        margin-top: 5rem;
        display: flex;
        flex-direction: column;
        background-color: black;
        height: 90vh;
        width: 40vw;
        margin-left: 10rem;

        @media only screen and (max-width:$bp-largest) {
            width: 70vw;
            margin-left: 0;
        }
    }

    &__top {
        height: 5rem;
        font-size: 2rem;
        text-align: center;
        color: white;
        border-bottom: 1px solid $color-grey-dark-2;
    }

    &__person {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 2rem 10rem;
        border-bottom: 1px solid $color-grey-dark-2;

        @media only screen and (max-width:$bp-largest) {
            padding: 2rem 4rem;
            display: block;
        }

        &:hover {
            background-color: $color-grey-dark-1;
        }

        &-data {
            display: flex;
            align-items: center;
        }

        &-username {
            font-size: 1.8rem;
            color: white;
            margin-left: 2rem;
        }

        &-img {
            height: 5rem;
            width: 5rem;
            object-fit: cover;
            border-radius: 50%;
        }

        &-btn {
            margin-top: 1rem;
            border-radius: 1rem;
            padding: 1rem 3rem;
            background-color: $color-btn;
            color: $color-white;
            border: none;
            cursor: pointer;
        }
    }
}