.posts {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;


    @media only screen and (max-width: $bp-small) {
        width: 100%;
    }


    &__not {
        width: 80%;
        margin-top: 20rem;
        text-align: center;

        @media only screen and (max-width:$bp-largest) {
            margin-left: 5rem;
        }


        &-found {
            color: white;
            font-size: 3rem;
            font-family: system-ui;

            @media only screen and (max-width:$bp-largest) {
                font-size: 2rem;
            }
        }

        &-explore {
            font-size: 2rem;
            color: white;
            font-family: system-ui;

            @media only screen and (max-width:$bp-largest) {
                font-size: 1.4rem;
            }
        }

    }
}

.postSmall {

    border: 1px solid $color-grey-dark-1;
    border-radius: 10px;
    color: white;
    margin-bottom: 10rem;
    width: 45rem;
    background-color: black;
    display: flex;
    flex-direction: column;

    @media only screen and (max-width: $bp-medium) {
        width: 50%;
        justify-content: center;
    }


    @media only screen and (max-width: $bp-small) {
        width: 70%;
        justify-content: center;
    }

    @media only screen and (max-width: 600px) {
        width: 100%;
        justify-content: center;
    }

    &__top {
        height: 6rem;
        display: flex;
        align-items: center;
        margin-left: 2rem;
        font-size: 14px;

        &-profile {
            width: 4rem;
            border-radius: 50%;
            margin-right: .5rem;
        }

        &-name {
            font-weight: bold;
            color: white;
        }
    }

    &__content {
        width: 100%;

        &-img {
            object-fit: cover;
            margin: auto;

            height: 100%;
            max-width: 100%;
        }
    }

    &__reaction {
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;

        &-icon {
            margin: 1rem;
            transform: scale(2);

        }
    }

    &__bottom {
        padding: 1rem 1rem 0rem 1rem;
        font-size: 1.2rem;

        &-likes {
            margin-bottom: 0.2em;
        }
    }

    &__comments {
        padding: 0rem 1rem 1rem 1rem;
        border-bottom: .1rem solid $color-grey-dark-1;
        max-height: 20rem;
        overflow: scroll;
        overflow-x: hidden;

        &-button {
            color: $color-grey-dark-1;
            font-size: 1.4rem;
            cursor: pointer;
            margin-bottom: .5rem;
        }

        &-time {
            color: $color-grey-dark-1;
            font-size: 1rem;
            margin-top: 1rem;

        }

        &-add {
            padding: 1.4rem;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &-input {
                border: none;
                width: 80%;
                background-color: inherit;
                color: $color-white;
                font-size: 1.8rem;
            }

            &-input:focus {
                outline: none;
            }

            &-btn {
                border: none;
                background-color: $color-primary-1;
                color: $color-btn;
                cursor: pointer;
            }
        }
    }
}

.addPost {
    position: absolute;

    width: 100%;
    height: 100%;
    background: rgba($color-black, 0.8);

    &__container {
        top: 50%;
        left: 50%;
        height: 85rem;
        width: 80rem;
        transform: translate(-50%, -50%);
        position: absolute;
        background-color: $color-grey-dark-1;
        opacity: 1;
        display: block;
        text-align: center;
        z-index: 100;
        border-radius: 2rem;
        display: flex;

        &-1 {
            width: 30rem;
        }
    }

    &__container-big {
        top: 50%;
        left: 50%;
        height: 85rem;
        width: 110rem;
        transform: translate(-50%, -50%);
        position: absolute;
        background-color: $color-grey-dark-1;
        opacity: 1;
        display: block;
        text-align: center;
        z-index: 100;
        border-radius: 2rem;
        display: flex;
    }

    &__container {
        align-items: center;
        flex-direction: column;
    }

    &__cancel {
        margin-left: 3rem;
        margin-top: 3rem;
        background-color: $color-alert-red;
        padding: 1rem 2rem;
        border-radius: 10px;
        z-index: 100;
        color: white;
        cursor: pointer;
    }

    &__title {
        padding: .5rem;
        border-bottom: 1px solid $color-grey-dark-2;
        color: white;
        font-size: 2rem;
    }

    &__input-text {
        background-color: green;
    }

    &__copper {
        width: 100%;
        height: 100%;
    }

    &__cropped {
        width: 80rem;
    }

    &__input-file {
        font-size: 1.25em;
        padding: 1rem;
        font-weight: 700;
        color: white;
        display: inline-block;
    }

    &__input-file[type=file]::file-selector-button {
        margin-right: 20px;
        border: none;
        background: $color-btn;
        padding: 10px 20px;
        border-radius: 10px;
        color: #fff;
        cursor: pointer;
        transition: background .2s ease-in-out;
    }

    &__input-file[type=file]::file-selector-button:hover {
        background: $color-btn-hover;
    }

    &__btn {
        border: none;
        margin-top: 2rem;
        background: $color-btn;
        padding: 1rem 2rem;
        border-radius: 10px;
        color: #fff;
        cursor: pointer;

        &-back {
            float: left;
            border: none;
            background: inherit;
            padding: .5rem 1rem;
            margin: 0.3em;
            border-radius: 10px;
            color: white;
            cursor: pointer;
        }

        &-next {
            float: right;
            border: none;
            background: inherit;
            padding: .5rem 1rem;
            margin: 0.3em;
            border-radius: 10px;
            color: $color-btn;
            cursor: pointer;
        }
    }


    &__user {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding: 2rem;

        &-img {
            width: 3rem;
            border-radius: 50%;
        }

        &-username {
            margin-left: 1rem;
            font-size: 1.6rem;
            font-weight: bold;
            color: white;
        }
    }

    &__textarea,
    &__textarea:focus {
        background-color: inherit;
        resize: none;
        font-size: 1.6rem;
        color: white;
        // border: none;
        outline: none;
    }
}


.post {
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: -10;


    &__background {
        position: absolute;
        width: 100vw;
        height: 100vh;
        z-index: -1;
    }

    &__container {
        position: fixed;
        width: 90%;
        top: 0;
        margin-top: 10rem;
        background-color: $color-black-2;
        display: flex;

        @media only screen and (max-width: $bp-large) {
            display: block;
            margin-top: 0;
            width: 100%;
            height: 100%;
            position: relative;
        }
    }

    &__img {
        object-fit: cover;
        max-height: 100%;
        max-width: 100%;


        &-container {
            width: 50%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            @media only screen and (max-width: $bp-largest) {
                width: 100%;
            }

            img {
                min-width: 100%;
                min-height: 100%;
            }
        }
    }

    &__details {
        display: flex;
        flex-direction: column;
        width: 50%;
        border-left: .1rem solid $color-grey-dark-1;

        @media only screen and (max-width: $bp-largest) {
            width: 95%;
        }
    }

    &__top {
        width: 100%;
        display: flex;
        padding: 1.4rem;
        display: flex;
        align-items: center;
        border-bottom: .1rem solid $color-grey-dark-1;

        &-picture {
            width: 5rem;
            height: 5rem;
            object-fit: cover;
            border-radius: 50%;
            box-sizing: border-box;
            background-image: linear-gradient(90deg, #f36229, rgb(126, 48, 228));
            padding: 1px;
        }

        &-username {
            margin-left: 2rem;
            color: white;
            font-size: 2rem;
        }

        &-icon {
            position: absolute;
            color: white;
            transform: scale(2);
            right: 3rem;
            cursor: pointer;
        }
    }

    &__content {
        display: flex;
        display: flex;
        align-items: center;
        color: white;

        &-author {
            padding: 2rem 0 2rem 2rem;
            font-weight: bold;
            font-size: 1.4rem;
        }

        &-description {
            padding: 2rem 2rem 2rem .5rem;
            font-size: 1.4rem;
            width: 100%;

            &-edit {
                display: flex;
            }
        }

        &-input {
            width: 80%;
            border: none;
            background-color: inherit;
            color: $color-white;
            font-size: 1.3rem;
            padding: 1rem;

            &:focus {
                outline-style: solid;
                outline-color: $color-grey-light-1;
            }
        }
    }

    &__comments {
        &-add {
            padding: 1.4rem;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &-input {
                border: none;
                background-color: inherit;
                color: $color-white;
                font-size: 1.8rem;
                border: 1px solid $color-grey-dark-1;
                padding: 3px;
            }

            &-input:focus {
                outline: none;
            }
        }
    }

    &__settings {

        position: absolute;
        float: right;
        right: 3rem;

        &-icon {
            margin-left: 2rem;
            color: white;
            transform: scale(2);
            cursor: pointer;
        }
    }
}