.sidebar {
    width: 24rem;
    height: 100%;
    font-size: 12px;
    padding: 1rem 1rem;
    position: fixed;
    background-color: black;
    border-right: 1px solid $color-grey-dark-1;
    z-index: 100;

    @media only screen and (max-width: $bp-largest) {
        width: 5rem;
        top: 0;
    }

    @media only screen and (max-width: $bp-small) {
        width: 100%;
        height: 8rem;
        top: 0;
    }

    &__menu {

        @media only screen and (max-width: $bp-small) {
            display: flex;
            margin-top: -8vh;
            align-items: center;
            width: 100%;
            justify-content: center;
        }

        &-element {
            justify-content: flex-start;
            display: flex;
            align-items: center;
            padding: 1.5rem;
            font-size: 2rem;
            font-family: system-ui;
            color: $color-white;
            font-weight: 100;

            @media only screen and (max-width:$bp-largest) {
                padding: .5rem;
                padding-top: 4rem;
            }

            p {
                @media only screen and (max-width:$bp-largest) {
                    opacity: 0;
                }
            }

            span {
                color: $color-white;
                cursor: pointer;

                @media only screen and (max-width: $bp-largest) {
                    display: none;
                }
            }

            &:first-child {

                @media only screen and (max-width: $bp-largest) {
                    padding-top: 4rem;
                }

                @media only screen and (max-width: $bp-small) {
                    padding-top: 4rem;
                }
            }
        }

        &__underline {
            border-bottom: 1px solid $color-grey-dark-1;
            margin: 0.5rem 2rem;
        }

        &-icon {
            transform: scale(2);
            margin-right: 2rem;
            color: $color-white;
            cursor: pointer;

            @media only screen and (max-width:$bp-largest) {
                transform: scale(1.6);
            }
        }
    }

    &__darker {
        filter: brightness(50%);
    }

}